import React, { Component } from "react";
import { connect } from "react-redux";
import Axios from "axios";
import { addPendingSlug } from "../../../services/utils/actions";
import { Redirect } from 'react-router'
import { GET_DEEPLINK_URL } from "../../../configs";

class DeepLink extends Component {

    state = {
        redirect: null
    }

    componentDidMount() {
        const alias = this.props.match.params.alias
        let url = (GET_DEEPLINK_URL + "/" + alias);
        Axios.post(url)
            .then((response) => {
                let to = response.data.slug ? "/stores/" + response.data.slug : "/";
                if (response.data.slug) {
                    this.props.addPendingSlug(response.data.slug)
                }
                this.setState({ redirect: to });
            })
            .catch(function (error) {
                this.setState({ redirect: "/" })
            });
    }
    render() {
        const { redirect } = this.state;

        if (redirect) {
            return <Redirect to={redirect} />;
        }

        return <div></div>
    }

}

const mapStateToProps = state => ({
    utils: state.utils
});

export default connect(mapStateToProps, { addPendingSlug })(DeepLink);

